<template>
  <div class="bookshelf">
    <van-nav-bar title="书架" />
    <div class="bookshelf-box">
      <van-tabs v-model="activeName">
        <van-tab title="漫画" name="a">
          <div class="bookbox">
            <div
              class="book fl"
              v-for="(item,index) in bookshelfData"
              :key="index"
              @click="GotoH(index)"
            >
              <div class="pho">
                <img class="auto-img" :src="item.img" alt />
              </div>
              <p class="one-text">{{item.name}}</p>
            </div>
            <div class="addition fl" @click="getTo({name:'home'})">
              <img src="../../assets/image/+.png" alt />
            </div>
          </div>
        </van-tab>
        <van-tab title="小说" name="b">
          <div class="bookbox">
            <div
              class="book fl"
              v-for="(item,index) in bookrackData"
              :key="index"
              @click="GotoX(index)"
            >
              <div class="pho">
                <img class="auto-img" :src="item.img" alt />
              </div>
              <p class="one-text">{{item.name}}</p>
            </div>
            <div class="addition fl" @click="getTo({name:'home'})">
              <img src="../../assets/image/+.png" alt />
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("bookshelfModule");

export default {
  name: "bookshelf",
  data() {
    return {
      activeName: "a",
      url: "",
      i: 0
    };
  },

  computed: {
    ...mapState(["bookshelfData", "bookrackData"])
  },

  methods: {
    GotoH(index) {
      this.i = index;
      this.$router.push({
        name: "contents",
        query: {
          url: this.bookshelfData[this.i].conUrl,
          mhUrl: this.bookshelfData[this.i].mhUrl,
          name: this.bookshelfData[this.i].name,
          mhName: this.bookshelfData[this.i].mhName,
          i: this.bookshelfData[this.i].i
        }
      });
    },

    GotoX(index) {
      this.i = index;
      this.$router.push({
        name: "matter",
        query: {
          url: this.bookrackData[this.i].conUrl,
          xsUrl: this.bookrackData[this.i].xsUrl,
          name: this.bookrackData[this.i].name,
          xsName: this.bookrackData[this.i].xsName,
          i: this.bookrackData[this.i].i
        }
      });
    },

    getTo(o) {
      this.$router.push(o);
    }
  },

  created() {
    //验证登录
    let userStatus = localStorage.getItem("user");

    if (userStatus) {
      let userInfo = JSON.parse(userStatus);

      //如果登录
      if (userInfo.isLogin) {
        
        //开启加载提示
        this.$toast.loading({
          duration: 0,
          message: "加载中..."
        });

        // 小说
        let matterData = localStorage.getItem("collectData");
        matterData = matterData ? JSON.parse(matterData) : [];
        // console.log(matterData);

        let bookrackData = matterData;

        bookrackData = bookrackData ? bookrackData : [];

        // console.log("bookrackData ==>> ", bookrackData);

        // 漫画
        let likeMovieData = localStorage.getItem("enshrineData");
        likeMovieData = likeMovieData ? JSON.parse(likeMovieData) : [];
        // console.log(likeMovieData);

        let bookshelfData = likeMovieData;

        bookshelfData = bookshelfData ? bookshelfData : [];

        // console.log("bookshelfData ==>> ", bookshelfData);

        this.$store.commit("bookshelfModule/getbookshelfData", bookshelfData);
        this.$store.commit("bookshelfModule/getbookrackData", bookrackData);

        this.$toast.clear();
      } else {
        //如果未登录
        this.$router.push({ name: "register" });
        this.$toast.clear();
      }
    } else {
      //未登录
      this.$router.push({ name: "register" });
      this.$toast.clear();
    }
  }
};
</script>
<style>
.bookshelf .van-nav-bar__title {
  font-size: 18px;
  color: #e4393c;
  font-weight: bolder;
}
.bookshelf-box .van-tabs--line .van-tabs__wrap {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.bookshelf-box .van-tab {
  color: black;
  font-size: 16px;
}
.bookshelf-box .van-tab--active {
  color: #e4393c;
  font-size: 18px;
  font-weight: bold;
}
.bookshelf-box .van-tabs--line .van-tabs__wrap {
  position: fixed;
  top: 46px;
  bottom: 0px;
  left: 0;
  right: 0;
}
</style>
<style lang="less" scoped>
.van-nav-bar {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0;
  right: 0;
}

.bookshelf {
  height: 100%;

  .bookshelf-box {
    height: calc(~" 100% - 50px ");
    position: fixed;
    top: 46px;
    bottom: 0px;
    right: 0;
    left: 0;
    background: url("../../assets/image/b.jpg") no-repeat center center;
    background-size: cover;
    .bookbox {
      padding: 15px 10px 10px;
      box-sizing: border-box;
      position: fixed;
      top: 92px;
      bottom: 50px;
      right: 0;
      left: 0;
      overflow-y: auto;
    }
    .book {
      width: calc(~"(100% - 45px ) / 3 ");
      margin-right: 18px;
      margin-bottom: 10px;
      &:nth-child(3n) {
        margin-right: 9px;
      }
      .pho {
        height: 145px;
        box-shadow: 3px 3px 0px 3px #999;
      }
      p {
        text-align: center;
        font-size: 16px;
        color: white;
        margin-top: 8px;
      }
    }
    .addition {
      width: calc(~"(100% - 45px ) / 3 ");
      height: 149px;
      border: 1px dashed black;
      position: relative;
      img {
        width: 30px;
        height: 30px;
        position: absolute;
        top: 50%;
        bottom: 50%;
        right: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}
</style>